.LoginWrap{
    height: 100%;

    .login{
        min-height: 100%;
        background:rgb(38,6,25);
    }

    // 
    .LoginHeaderWrap{
        display: block;
        width: 100%;
        position: relative;

        .LoginHeader{
            width: 100%;
            height: 50px;
            background-color: rgb(81,31,40);
            position: relative;
            text-align: center;
            line-height: 50px;

            span{
                display: inline-block;
                height: 100%;
                font-size: 22px;
                color:#fff;
                font-weight: bold;
            }

        }
    }

    // 
    .LoginMainWrap{
        position: relative;
        width: 100%;
        box-sizing:border-box;
        padding:40px 0px;
        
        .LoginMain{
            width: 100%;
            // height: 30vh;
            // background:red;

            .LoginForm{
                width: 100%;
                position: relative;
                margin-top: 30px;

                .phone , .code{
                    margin: auto;
                    width: 67%;
                }

                .phone{
                    
                    width: 67%;
                    height: 100%;
                    position: relative;
                    display: table;
                        .code{
                            display: table;
                            position: absolute;
                            top: 0;
                            right: -1%;
                            width: 42%;
                            height: 100%;
                            background:rgb(253, 130, 99);
                            border-top-right-radius: 5px;
                            border-bottom-right-radius: 5px;
                            display:table-cell;
                            vertical-align:middle;
                            text-align: center;
                            line-height:40px;
                            // line-height: 250%;
                            color: #fff;
                            font-size: 14px;
                        }
                        .Ucode{
                            display: table;
                            position: absolute;
                            top: 0;
                            right: -1%;
                            width: 42%;
                            height: 100%;
                            background:rgb(81,31,40);
                            border-top-right-radius: 5px;
                            border-bottom-right-radius: 5px;
                            display:table-cell;
                            vertical-align:middle;
                            text-align: center;
                            line-height: 40px;
                            // line-height: 250%;
                            color: gray;
                            font-size: 14px;
                        }
                    
                    }

                    
                


                input{
                    display: block;
                    width: 100%;
                    height: 40px;
                    text-indent: 12px;
                    border-radius: 3px;
                    color: #fff;
                    border: 2px solid rgb(58,18,32);
                    outline: none;
                    background:transparent;
                    // background: url(./images/PhoneWrap.png) no-repeat center center;
                    // background-size:100% 100%;
                    font-size: 14px;
                    margin: auto;
                }
                input::-webkit-input-placeholder{
                    color: rgb(255,153,102);
                }

            }

            // .LoginBtn{
            //     width: 100%;
            //     position: relative;
            //     margin-top: 30px;

            //     .LoginB{
            //         display: inline-block;
            //         position: absolute;
            //         top: 5%;
            //         left: 16%;
            //         margin: auto;
            //         width: 68%;
            //         height: 82%;
            //         // background:rgba(138, 42, 42, 0.3);
            //     }
            // }

            .LoginBtnWrap{
                width: 68%;
                height: 50px;
                margin-top: 30px;
                margin-left: auto;
                margin-right: auto;
                border-radius: 50px;
                box-sizing: border-box;
                padding: 5px 5px;
                background:linear-gradient(to bottom , rgb(253,229,110) , rgb(253,143,1));
            
                .LoginBtn{
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(to right , rgb(252,215,38) , rgb(253,174,22));
                    border-radius: 50px;
                    text-align: center;
                    line-height: 40px;

                    span{
                        color: #000;
                        font-size: 18px;
                        font-weight: bold;
                    }
                }
            }
            .unLoginBtnWrap{
                width: 68%;
                height: 50px;
                margin-top: 30px;
                margin-left: auto;
                margin-right: auto;
                border-radius: 50px;
                box-sizing: border-box;
                padding-bottom: 5px;
                background:linear-gradient(to bottom , rgb(128,128,128) , rgb(102,102,102));
            
                .LoginBtn{
                    width: 100%;
                    height: 100%;
                    background: rgb(128,128,128);
                    border-radius: 50px;
                    text-align: center;
                    line-height: 45px;

                    span{
                        color: #000;
                        font-size: 18px;
                        font-weight: bold;
                    }
                }
            }


        }
    }
}