.HPWrap{
    height: 100%;
    .HPMain{
        min-height: 100%;
        background:url(./images/HPBck.jpg) no-repeat top;
        background-size:100% 100%  ;


        //
        .HP_HeaderWrap{
            width: 100%;
            position: relative;
            overflow: hidden;

            .HP_HeaderTit1{
                width: 100%;
                height: 59px;
                position: relative;
                top:0%;
                font-size: 20px;
                text-align: center;
                line-height: 80px;
                margin-top: 22px;


                .Hp_HeaderTit1Text{
                    color: rgb(252, 217, 119)
                }
                
            }
            .HP_HeaderTit1::before{
                content: '';
                width: 10%;
                height: 3px;
                background: rgb(252, 217, 119);
                position: absolute;
                top: 64%;
                left: 9%;
            }
            .HP_HeaderTit1::after{
                content: '';
                width: 10%;
                height: 3px;
                background: rgb(252, 217, 119);
                position: absolute;
                top: 64%;
                right: 9%;
            }

            .HP_HeaderTit2{
                width: 80%;
                margin: 0 auto;
            }
        }

        //
        .HPUserInfo_Container{
            width: 100%;
            margin-top: 43px;
            position: relative;

            //
            .HPUserInfo_TitWrap{
                width: 100%;
                position: relative;

                .HPUserInfo_AvaWrap{
                position: absolute;
                top: -48%;
                left: 42%;
                width: 60px;
                height: 60px;
                background: rgb(253,130,99);
                border-radius: 100%;

                img{
                    width: 82%;
                    margin-left: 6px;
                    margin-top: 5px;
                    border-radius: 100%;
                }
                
            }
                .HPUserInfo_IdWrap{
                    position: absolute;
                    top: 33%;
                    left: 0%;
                    width: 100%;
                    height: 40px;
                    line-height: 40px;
                    text-align: center;


                    span{
                        font-size: 16px;
                        color: #fff;                
                    }
                }

            }
            // 
            .HPUserInfo_MainWrap{
                width: 83%;
                margin: 0 auto;
                display: flex;
                margin-top: -3px;

                //
                .HPUserInfo_Item1{
                    width: 3.4%;
                    height: 100%;
                    
                    img{
                        width:100%;
                        height: 100%;
                    }
                }

                //
                .HPUserInfo_Item2{
                    width: 93.4%;
                    height: 100%;
                    box-sizing: border-box;
                    padding: 18px 10px;

                    //
                    .HPUserInfo_MainTit{
                        width: 100%;
                        display: flex;
                        justify-content: space-between;
                        line-height: 28px;

                        .HPUserInfo_MainText{
                            font-size: 13px;
                            color: #fff;
                        }
                        .HPUserInfo_MainBtn{
                            width: 21%;
                            line-height: 28px;
                            border-radius: 50px;
                            background:rgb(252,216,38);
                            color:#000;
                            font-size: 13px;
                            text-align: center;
                        }
                    }

                    //
                    .HPUserInfo_ProGWrap{
                        width: 100%;
                        margin-top: 20px;
                        position: relative;

                        span{
                            display:block;
                            border-radius: 50px;
                        }
                        p{
                            display: inline-block;
                            position: absolute;
                            top: 50%;
                            margin-top: 10px;
                            width: 15%;
                            height: 21px;
                            text-align: center;
                            color: #fff;
                            font-size: 13px;
                        }
                        .HPUserInfo_ProItem1{
                            width: 100%;
                            height: 10px;
                            background-color:rgb(76,64,71);
                        }
                        .HPUserInfo_ProItem2{
                            width: 50%;
                            height: 10px;
                            background-color:rgb(252,216,38);
                        }
                    }

                    //
                    .HPUserInfo_BtnWrap{
                        width: 100%;
                        height: 50px;
                        background: linear-gradient(to bottom , rgb(253,226,108) , rgb(253,143,3));
                        border-radius: 50px;
                        box-sizing: border-box;
                        padding: 5px 10px;
                        margin-top: 32px;

                        .HPUserInfo_BtnLogin{
                            width: 100%;
                            height: 100%;
                            background: linear-gradient(to right , rgb(252,217,99) , rgb(253,174,22));
                            text-align: center;
                            border-radius: 50px;
                            color: #000;
                            line-height: 40px;
                            font-size: 18px;
                            font-weight: bold;
                        }
                    }

                    //
                    .HPUserInfo_BckPayWrap{
                        width: 60%;
                        margin-top: 17px;
                        margin-left: auto;
                        margin-right: auto;
                        line-height: 13px;
                        color: rgb(253,130,99);
                        text-align: center;
                        font-size: 14px;
                        border-bottom: 1px solid rgb(253,130,99);
                        padding-bottom: 5px;
                        
                    }
                }

                //
                .HPUserInfo_Item3{
                    width: 3.4%;
                    height: 100%;
                }
            }

            //
            .HPUserInfo_Bottom{
                width: 100%;
                margin-top: -3px;
            }
        }

        //
        .HPProduct_Container{
            width: 100%;
            margin-top: 50px;


            //
            .HPProduct_TitWrap{
                width: 100%;
                height: 100%;
                position: relative;

                span{
                    display: block;
                    position: absolute;
                    top: 31%;
                    left: 0%;
                    width:100%;
                    text-align: center;
                    font-size: 18px;
                    color: #fff;
                    font-weight: bold;
                }

                img{
                    width: 100%;
                    height: 100%;
                }
                .HPProduct_TitItem1 , .HPProduct_TitItem2{
                    position: absolute;
                    top: 39%;
                    width: 8%;

                }
                .HPProduct_TitItem1{
                    left:22%;
                }
                .HPProduct_TitItem2{
                    right: 22%;
                }
            }

            //
            .HPProduct_MainWrap{
                width: 83%;
                height: 150px;
                margin: 0 auto;
                display: flex;
                margin-top: -3px;

                //
                .HPProduct_Item1 , .HPProduct_Item3{
                    width: 4.3%;
                    height: 100%;
                    background: url(./images/HPUserInfo_Bck1.png) no-repeat;
                    background-size: 100% 100%;
                    
                    img{
                        width:100%;
                        height: 100%;
                    }
                }
                //
                .HPProduct_Item2{
                    width: 93.4%;
                    height: 100%;
                    background: url(./images/HPProduct_Img.png) no-repeat center;
                    background-size: 100% 100%;
                }
            }
            //
            .HPProduct_Bottom{
                width: 100%;
            }
        }

        // 
        .HPActStep_Container{
            width: 87%;
            margin-top: 35px;
            margin-bottom: 10px;
            margin-left: auto;
            margin-right: auto;
            overflow: hidden;

            //
            .HPActStep_Header{
                position: relative;
                width: 100%;
                height: 50px;
                line-height: 50px;
                background-color: rgb(81,31,40);
                border-top-left-radius:5px;
                border-top-right-radius:5px;
                border-bottom:3px dashed #000;
                text-align:center;

                span{
                    color:rgb(255,179,146);
                    font-size: 20px;
                    font-weight: bold;
                }

                .HPActStep_HeaderItem1{
                    width: 8%;
                    position: absolute;
                    top: -2%;
                    left: 25%;   
                }
                .HPActStep_HeaderItem2{
                    width: 8%;
                    position: absolute;
                    top: -2%;
                    right: 25%;  
                }
            }

            //
            .HPActStep_NewUserWrap , .HPActStep_OldUserWrap{
                width: 100%;
                position: relative;
                background:rgb(81,31,40);
                border-bottom:3px dashed #000;
                box-sizing:border-box;
                padding-top:15px;
                color: #fff;

                .CriItem1{
                    position: absolute;
                    top: -5%;
                    left: -2.6%;
                    width: 16px;
                    height: 16px;
                    border-radius: 100%;
                    background-color: #000;;
                }
                .CriItem2{
                    position: absolute;
                    top: -5%;
                    right: -2.6%;
                    width: 16px;
                    height: 16px;
                    border-radius: 100%;
                    background-color: #000;;
                }

                .HPActStep_NewUserTit{
                    width: 52%;
                    height: 27px;
                    background:rgb(115,51,52);
                    text-align:center;
                    margin-left:auto;
                    margin-right:auto;
                    border-radius:50px;
                    line-height:27px;
                    border:2px solid rgb(210, 105, 84);
                    font-size: 15px;
                }

                .HPActStep_NewUserMain{
                    width: 80%;
                    margin-top:15px;
                    margin-bottom:17px;
                    margin-left:auto;
                    margin-right:auto;
                    font-size: 15px;


                    li{
                        margin-bottom: 5px;
                    }
                }
            }
            .HPActStep_OldUserWrap{
                border-bottom:03px dashed transparent;
                border-top-left-radius:5px;
                border-top-right-radius:5px;
            }
        }

        //
        .HPActRuler_Container{
            width: 87%;
            position: relative;
            margin-top: 25px;
            margin-left: auto;
            margin-right: auto;

            //
            .HPActRuler_Header{
                position: relative;
                width: 100%;
                height: 50px;
                line-height: 50px;
                background-color: rgb(81,31,40);
                border-top-left-radius:5px;
                border-top-right-radius:5px;
                border-bottom:3px dashed #000;
                text-align:center;

                span{
                    color:rgb(255,179,146);
                    font-size: 20px;
                    font-weight: bold;
                }

                .HPActRuler_HeaderItem1{
                    width: 8%;
                    position: absolute;
                    top: -2%;
                    left: 25%;   
                }
                .HPActRuler_HeaderItem2{
                    width: 8%;
                    position: absolute;
                    top: -2%;
                    right: 25%;  
                }
            }

            //
            .HPActRuler_MainWrap{
                position: relative;
                width: 100%;
                background:rgb(81,31,40);
                border-bottom-left-radius:5px;
                border-bottom-right-radius:5px;
                box-sizing:border-box;
                padding-top:8px;
                padding-bottom:10px;
                color: #fff;

                .HPActRuler_Main{
                    width: 80%;
                    margin-top:15px;
                    margin-bottom:17px;
                    margin-left:auto;
                    margin-right:auto;
                    font-size: 15px;

                    li{
                        margin-bottom:11px;
                    }
                }


            }
        }

        //  
        .HPBotLogo_Container{
            width: 100%;
            position: relative;
            margin-top: 35px;
        }





        

    }
    
}
@media screen and (max-width : 320px){
    .HPWrap .HPMain .HPUserInfo_Container .HPUserInfo_MainWrap .HPUserInfo_Item2 .HPUserInfo_MainTit .HPUserInfo_MainText{
        font-size: 12px;
    }
    .HPWrap .HPMain .HPUserInfo_Container .HPUserInfo_MainWrap .HPUserInfo_Item2 .HPUserInfo_BckPayWrap{
        width: 72%;
        margin-top: 9px;
    }
    .HPWrap .HPMain .HPUserInfo_Container .HPUserInfo_MainWrap .HPUserInfo_Item2{
        padding: 8px 10px;
    }
}
