.ActiveWrap{
    height: 100%;
    
    .Active{
        min-height: 100%;
        background:url(./images/ActiveBck.png) no-repeat top;
        background-size: cover;
    }

    // 
    .ActHeaderWrap{
        width: 100%;
        position: relative;

        .ActHeader{
            width: 100%;
            position: relative;
        }
    }

    // 
    .ActUserWrap{
        width:100%;
        position: relative;
        box-sizing: border-box;
        padding: 0px 0px;
        margin-top: 10px;

        .ActUser{
            position: absolute;
            display: flex;
            flex-direction: inherit;
            top: 15%;
            left: 19.6%;;
            width:62%;
            height: 69%;
            // background:rgba(15,151,31,.3);

            .ActUserImg , .ActUserId{
                display: inline-block;
               
                height: 87%;
            }
            .ActUserImg{
                width: 20.6%;
                // background:rgba(15,53,51,.3);
                border-radius: 50px;
                background:url(./images/img.png) no-repeat center center;
                background-size:100% 100%;
            }
            .ActUserId{
                width: 72%;
                line-height:1.2rem;
                // margin-left: 10px;
                color: #fff;
                text-align: center;
                // background:blue;
                font-size: 0.48rem;
            }
        }
    }
    // 
    .ActiveImg{
        width: 100%;
        position: relative;
        margin-bottom: 0.6rem;

        .ImgWrap{
            width: 100%;
            height: 4.5rem;
            margin-top: 0.3rem;
            // background:red;

            .Img{
                position:relative;
                width: 80.6%;
                height: 100%;
                // background: blue;
                margin:0 auto;
                border: 3px solid rgb(92,92,255);
                box-shadow: 0px 0px 14px 2px rgb(92,92,255);
                box-sizing: border-box;
                // padding:14px 20px;
                padding:0.38rem 0.48rem;

                .ImgMain{
                    width: 100%;
                    height: 100%;
                    background:red;
                    background:url(./images/IMG_item1.png) no-repeat center center;
                    background-size:100% ;
                }

                span{
                    position: absolute;
                    display: inline-block;
                    width: 7%;
                    height: 14%;
                    background:#fff;
                }

                span:nth-child(1){
                    background:url(./images/Top_Left.png) no-repeat center center;
                    background-size : 100% ;
                    top: -3.2%;
                    left: -1.5%;
                }
                span:nth-child(2){
                    background:url(./images/Top_Right.png) no-repeat center center;
                    background-size : 100% ;
                    top: -3.2%;
                    right: -1.5%;
                }
                span:nth-child(3){
                    background:url(./images/Bottom_Left.png) no-repeat center center;
                    background-size : 100% ;
                    left: -1.5%;
                    bottom: -3.2%;
                }
                span:nth-child(4){
                    background:url(./images/Bottom_Right.png) no-repeat center center;
                    background-size : 100% ;
                    bottom: -3.2%;
                    right: -1.5%;
                }

            }
        }

        .ActiveImgWrap{
            width: 100%;
            height: 5vh;
            background: url(./images/ActiveImgTil.png) no-repeat center center;
            background-size: 100% 100%;
            margin: auto;
        }
    }
    // 
    .ActivePro{
        width: 100%;
        position: relative;
        margin-top: -1%;

        .ActivePro-title{
            width: 100%;;
            color: #fff;
            text-align: center;
            font-size: .40rem;
        }

        .ActivePro-main{
            position: relative;
            width: 63%;
            line-height: 1rem;
            margin: auto;

            span{
                display: inline-block;
                position: relative;
                width: 100%;
                line-height: 0.1rem;
                height: 0.18rem;
                border-radius: 10px;
                background:rgb(57,57,67);
            }
            .finish{
                position: absolute;
                top: 0px;
                left: 0px;
                width: 50%;
                height: 100%;
                background:rgb(255,199,0);
            }

            .ActiveProOne{
                position: absolute;
                // display: flex;
                top: 56%;
                left: 48%;
                width: 12%;
                background: transparent;      
            }

            .ActiveProTwo{
                position: absolute;
                // display: flex;
                top: 56%;
                right: -13px;
                width: 12%;
                background: transparent;

                .ActiveProCri{
                    background: transparent;
                }
            }
            .ActiveProCri{
                width: 0.35rem;
                height: 0.35rem;
                border-radius: 100%;
                // border:1px solid black;
                // box-shadow: 0px 0px  20px 7px rgb(255,255,0);
                // background: rgba(255, 255, 0,.5);
                // background:rgb(255,255,255)
                background: rgb(255,255,0);
            
            }
            .ActiveProCri1{
                width: 0.25rem;
                height: 0.25rem;
                border-radius: 100%;
                background:transparent;
            }
            .ActiveProOP{
                display: block;
                font-size: 0.4rem;
                color: #fff;
                background: transparent;
                margin-top: 73%;
            }
        }
    }
    // 
    .ActPayBck{
        position: relative;
        width: 100%;
        height: 6rem;

    }
    .ActPayWrap{
        width: 45%;
        line-height: 0.08rem;
        // margin: 0.48rem auto;
        box-sizing: border-box;
        padding: 50px 0px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 11px;
        

        .ActPay{
            width: 100%;
            height: 100%;
            // background:Red;
            text-align: center;
            border-bottom: 1px solid rgb(255,199,0);

            span{
                color:rgb(255,199,0);
                line-height: 0.55rem;
                font-size: .48rem;
            }
        }
    }
    // 
    .ActBtnWrap{
        position: fixed;
        width: 100%;
        bottom: 0;
        margin-bottom: 17%;
        .ActBtn{
            width: 100%;
        }
        .ActB{
            display: inline-block;
            position: absolute;
            top: 0%;
            margin: auto;
            width: 100%;
            height: 3rem;
            // background:rgba(138, 42, 42, 0.3);
            background: #ffc400;

            p{

                height: 100%;
                line-height: 1.7rem;
                text-align:center;
                font-size:.48rem;
                font-weight: 600;
                color: #000;
            }
        }
    }
    // 
    .imgWrap{
        position: fixed;;
        bottom: 0;
        left:0;
        width: 100%;
        height: 100%;
        background:rgba(0,0,0,.91);
    }
    // 
    .saveImgWrap{
        position: fixed;
        bottom: 0;
        left: 0;
        width: 100%;
        background:white;
        z-index: 99;
        box-sizing:border-box;
        padding: 0.1rem 0;
        margin:0 auto;

        .saveImg{
            width: 100%;
            height: 100%;
            // background:red;
            margin:0 auto;
            // display: flex;
            img{
                display: block;
                margin:0 auto;
            }

            span{
                font-size: 0.5rem;
                display: block;
                // width: 20%;
                margin-top: 10px;
                text-align: center;
            }
        }
    }
    // 
    .codeWrap{
        display: none;
        position: fixed;;
        bottom: 0;
        left:0;
        width: 100%;
        height: 100%;
        background:rgba(0,0,0,.91);
        z-index: 999;    
    }
    .saveCode_Wrap{
        position: relative;
        width: 100%;
        height: 100%;
    }
    .saveCodeWrap{
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9999;

        .closeCode{
            display: none;
            position: fixed;
            top: 2.6%;
            right: 9%;

            span{
                display: inline-block;
                width: 30px;
                height: 30px;
                // background: url(./images/)
                background: url(./images/Act_Suc_Close_item.png) no-repeat center center;
                background-size: 100%;
            }
        }

        canvas{
            width: 70%;
            margin-left: 55px;
            border:1px solid #000000;
            background-color: #000
        }
        #qrcode{
            width:100px; 
            height:100px;
            display: none;
        }
        .codeImg{
            margin-top: 1%;
            width: 77%;
            margin-left: 11.5%;
            z-index: 9999;
        }
    }

    // 
    .SaveImgBtnWrap{
        display: none;
        position: fixed;
        bottom: 5.5%;
        left: 0;
        width: 100%;
        line-height: 1rem;
        // background:red;
        box-sizing: border-box;
        padding: 12px 63px;
        z-index: 999999;

        .SaveImgBtn{
            
            width: 100%;
            background:#ff6700;
            border-radius: 30px;
            text-align: center;

            span{
                color: #fff;
                font-size: 0.58rem;
                line-height: 1.2rem;
            }
        }
    }

    // 
        //
    .ActSucWrap{
        display: none;
        position: fixed;;
        bottom: 0;
        left:0;
        width: 100%;
        height: 100%;
        background:rgba(0,0,0,.91);   
    } 
        // 
    .ActSuccessWrap{
        display: none;
        position: fixed;
        top: 33%;
        left: 0;
        width: 100%;

        .ActSuccess_Wrap{
            position: relative;
            width: 100%;
            height: 100%;

            .ActSuccess_btn{
                display: inline-block;
                position: absolute;
                top: 64%;
                left: 23%;
                margin: auto;
                width: 54%;
                height: 21%;
                text-align: center;
                font-size: 0.48rem;
                line-height: 7vh;
            }
        }
        .CloseActSuccess{
            position: absolute;
            top: 1%;
            right: 12%;

            span{
                display: inline-block;
                width: 30px;
                height: 30px;
                // background: url(./images/)
                background: url(./images/Act_Suc_Close_item.png) no-repeat center center;
                background-size: 100%;
            }
        }
        // .CloseActSuccess{
        //     position: relative;
        //     width: 100%;
        //     text-align: center;

        //     .CloseActSuccess_btn{
        //         display: inline-block;
        //         position: absolute;
        //         top: 0%;
        //         left: 44%;
        //         margin: auto;
        //         width: 12%;
        //         height: 91%;
        //     }
        // }
    }
}
